"use client";

import { useRouter } from "next/navigation";
import { 
  Coins, 
  ArrowLeft, 
  Zap, 
  ShieldCheck, 
  Gift, 
  ChevronRight, 
  CheckCircle2,
  TrendingUp
} from "lucide-react";

export default function EmpireManual() {
  const router = useRouter(); // ✨ Initialize router for "Back" functionality

  return (
    <div className="min-h-screen bg-white font-sans selection:bg-amber-100 pb-20">
      
      {/* --- NAVIGATION --- */}
      <nav className="fixed top-0 left-0 right-0 bg-white/80 backdrop-blur-md z-50 border-b border-gray-100 px-6 py-4">
        <div className="max-w-5xl mx-auto flex justify-between items-center">
          {/* ✨ FIXED: Now uses router.back() to go exactly where the user came from */}
          <button 
            onClick={() => router.back()} 
            className="flex items-center gap-2 text-gray-900 font-black uppercase text-xs tracking-tighter hover:text-amber-500 transition-colors"
          >
            <ArrowLeft size={16} /> Back
          </button>
          
          <div className="flex items-center gap-2">
            <div className="bg-amber-500 p-1.5 rounded-lg text-white">
              <Coins size={14} fill="currentColor" />
            </div>
            {/* ✨ FIXED: Removed "Manual v1" text */}
          </div>
        </div>
      </nav>

      {/* --- HERO SECTION --- */}
      <header className="pt-32 pb-16 px-6 text-center bg-gray-50 rounded-b-[4rem]">
        <div className="max-w-3xl mx-auto">
          <div className="inline-flex items-center gap-2 bg-amber-500/10 text-amber-500 border border-amber-500/20 px-4 py-2 rounded-full mb-6">
            <Gift size={16} />
            <span className="text-[10px] font-black uppercase tracking-[0.2em]">₦50 Welcome Bonus Active</span>
          </div>
          <h1 className="text-5xl md:text-7xl font-black text-gray-900 uppercase tracking-tighter leading-[0.9] mb-6">
            The Digital Gold <br />of <span className="text-amber-500">Naija Hustle.</span>
          </h1>
          <p className="text-gray-500 text-lg md:text-xl font-medium max-w-2xl mx-auto leading-relaxed">
            Empire Coins are more than just points. They are a universal currency designed to reward loyalty across the entire StoreLink network.
          </p>
        </div>
      </header>

      {/* --- QUICK START BONUS CARD --- */}
      <section className="px-6 -mt-10">
        <div className="max-w-4xl mx-auto bg-gray-900 rounded-[3rem] p-8 md:p-12 shadow-2xl relative overflow-hidden border border-white/5">
           <div className="absolute top-0 right-0 p-10 opacity-20 transform translate-x-10 -translate-y-10">
              <Coins size={200} className="text-amber-500" />
           </div>
           
           <div className="relative z-10 flex flex-col md:flex-row items-center gap-8">
              <div className="flex-1 text-center md:text-left">
                 <h2 className="text-white font-black text-2xl md:text-3xl uppercase tracking-tight mb-4">
                    Claim your <span className="text-amber-500">₦50 Start-Up</span> Capital
                 </h2>
                 <p className="text-gray-400 text-sm mb-6 leading-relaxed">
                    Every new Empire Wallet starts with 50 Coins. Simply sync your WhatsApp number in the gold pill on the homepage to activate your account.
                 </p>
                 <div className="flex flex-wrap gap-4 justify-center md:justify-start">
                    <div className="bg-white/5 border border-white/10 px-4 py-2 rounded-xl text-white text-[10px] font-black uppercase tracking-widest">
                       1 Coin = ₦1
                    </div>
                    <div className="bg-white/5 border border-white/10 px-4 py-2 rounded-xl text-white text-[10px] font-black uppercase tracking-widest">
                       No Expiry
                    </div>
                 </div>
              </div>
              <div className="w-full md:w-auto">
                 <div className="bg-amber-500 p-6 rounded-[2rem] text-center shadow-xl shadow-amber-500/20">
                    <p className="text-amber-900 font-black uppercase text-[10px] tracking-widest mb-1">Instant Balance</p>
                    <p className="text-4xl font-black text-white tracking-tighter">₦50.00</p>
                 </div>
              </div>
           </div>
        </div>
      </section>

      {/* --- THE THREE PILLARS --- */}
      <section className="py-24 px-6 max-w-5xl mx-auto">
        <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
          
          <div className="space-y-4">
            <div className="w-12 h-12 bg-gray-900 rounded-2xl flex items-center justify-center text-white">
              <Zap size={24} fill="currentColor" />
            </div>
            <h3 className="font-black text-lg uppercase tracking-tight">1. Earn Everywhere</h3>
            <p className="text-gray-500 text-sm leading-relaxed font-medium">
              Shop from any verified StoreLink vendor. Look for the <span className="text-amber-600 font-bold">Empire Badge</span> on products to see exactly how many coins you will earn per purchase.
            </p>
          </div>

          <div className="space-y-4">
            <div className="w-12 h-12 bg-amber-500 rounded-2xl flex items-center justify-center text-white">
              <TrendingUp size={24} />
            </div>
            <h3 className="font-black text-lg uppercase tracking-tight">2. Grow Your Balance</h3>
            <p className="text-gray-500 text-sm leading-relaxed font-medium">
              Your coins are saved automatically to your phone number. No passwords, no logins. Just sync and watch your digital gold grow with every hustle.
            </p>
          </div>

          <div className="space-y-4">
            <div className="w-12 h-12 bg-emerald-500 rounded-2xl flex items-center justify-center text-white">
              <CheckCircle2 size={24} />
            </div>
            <h3 className="font-black text-lg uppercase tracking-tight">3. Redeem as Cash</h3>
            <p className="text-gray-500 text-sm leading-relaxed font-medium">
              Ready to checkout? Use your balance to get instant discounts. Whether it's ₦500 or ₦5,000, your coins spend exactly like Naira at participating stores.
            </p>
          </div>

        </div>
      </section>

      {/* --- FAQ SECTION --- */}
      <section className="py-20 px-6 bg-gray-50 rounded-[4rem]">
        <div className="max-w-3xl mx-auto">
          <h2 className="text-3xl font-black text-gray-900 uppercase tracking-tighter text-center mb-12">Common Questions</h2>
          
          <div className="space-y-4">
            {[
              { q: "Is there a limit to how many coins I can earn?", a: "No. The bigger your hustle, the bigger your Empire. There is no cap on earning." },
              { q: "Do my coins expire?", a: "Never. Your coins are yours forever until you choose to spend them on a discount." },
              { q: "Can I transfer coins to a friend?", a: "Currently, coins are tied to your WhatsApp number to ensure security and prevent fraud." },
              { q: "Which stores accept Empire Coins?", a: "All Premium and Diamond StoreLink vendors support the Empire Economy." }
            ].map((item, i) => (
              <div key={i} className="bg-white p-6 rounded-[2rem] border border-gray-100">
                {/* ✨ FIXED: Changed <p> to <div> to solve the Hydration/Nesting error */}
                <div className="font-black text-gray-900 uppercase text-[11px] tracking-widest mb-2 flex items-center gap-2">
                  <div className="w-1.5 h-1.5 bg-amber-500 rounded-full shrink-0" /> {item.q}
                </div>
                <p className="text-gray-500 text-sm font-medium leading-relaxed">{item.a}</p>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* --- VENDOR CTA --- */}
      <section className="pt-24 px-6 text-center">
        <div className="max-w-2xl mx-auto bg-emerald-600 rounded-[3rem] p-10 md:p-16 text-white shadow-2xl shadow-emerald-200">
          <ShieldCheck size={48} className="mx-auto mb-6" />
          <h2 className="text-3xl font-black uppercase tracking-tight mb-4">Are you a Vendor?</h2>
          <p className="text-emerald-100 text-sm font-medium mb-8 leading-relaxed">
            Join the network and start issuing Empire Coins to your customers today. Boost retention and grow your sales by 30% through our loyalty engine.
          </p>
          <button 
            onClick={() => router.push('/dashboard/loyalty')}
            className="inline-flex items-center gap-3 bg-white text-emerald-600 px-8 py-4 rounded-2xl font-black uppercase text-xs tracking-widest hover:bg-emerald-50 transition-all"
          >
            Activate My Store <ChevronRight size={18} />
          </button>
        </div>
      </section>

    </div>
  );
}